.information-container {
  margin-left: 24px;
  width: max-content;

  ul {
    gap: 30px;
    width: max-content;
    color: rgba($color: $color-white, $alpha: 0.75);
    margin-bottom: 5px;
  }

  li:not(:first-child) {
    list-style: disc;
  }

  h2 {
    color: $color-white;
  }
}
