.play-container {
  width: 100%;
  height: calc(100% + 8px);
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba($color: $color-black, $alpha: 0.75);
  z-index: 5;

  button {
    cursor: pointer;
    width: 117px;
    height: 48px;
    padding: 0 24px 0 9px;
    border-radius: 28px;
    background-color: rgba($color: $color-white, $alpha: 0.25);
    color: $color-white;
  }

  img {
    width: 30px;
    margin-right: 19px;
  }
}
