.poster-container {
  width: 280px;
  height: auto;
  position: relative;

  .image-div {
    position: relative;
    width: 100%;
    height: 174px;
    margin-bottom: 8px;

    &:hover {
      .play-container {
        display: flex;
      }
    }

    .image {
      width: 100%;
    }
  }

  .play-container {
    display: none;
  }
}
