$color-red: #fc4747;
$color-black: #10141e;
$color-grey: #5a698f;
$color-blue: #161d2f;
$color-white: #ffffff;

* {
  font-family: "Outfit", sans-serif;
  font-style: normal;
}

h1 {
  font-weight: 300;
  font-size: 32px;
  line-height: 40px;
  letter-spacing: -0.5px;
}

h2 {
  font-weight: 300;
  font-size: 24px;
  line-height: 30px;
}

h3 {
  font-weight: 500;
  font-size: 24px;
  line-height: 30px;
}

h4 {
  font-weight: 500;
  font-size: 18px;
  line-height: 23px;
}

.body1 {
  font-weight: 300;
  font-size: 15px;
  line-height: 19px;
}

.body2 {
  font-weight: 300;
  font-size: 13px;
  line-height: 16px;
}

.border-radius {
  border-radius: 8px;
}

.flex {
  display: flex;

  &__alignCenter {
    align-items: center;
  }

  &__column {
    flex-direction: column;
  }

  &__justifyCenter {
    justify-content: center;
  }
}

.recommanded-div {
  display: flex;
  flex-wrap: wrap;
  gap: 32px 40px;
}
