.trending-container {
  position: relative;
  width: 470px;
  height: 230px;

  &:hover {
    .play-container {
      display: flex;
    }
  }

  .imageBackground {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }

  .play-container {
    display: none;
  }
}
